<template>
  <div id="main">
    <el-row style="background-color:#F2F2F2;margin-top:10px;margin-bottom:5px;">
        <el-col :span="3">
            <el-button type="primary" @click='handleAdd' icon="plus">新增</el-button> 
        </el-col>
    </el-row>
    <el-row>
        <el-col :span="24">
          <el-table
                    element-loading-text="加载数据"
                    :data="TypeDatas"
                    style="width:100%;text-align:center"  
                    empty-text="暂无数据"
                    highlight-current-row
                    >
            <el-table-column label="ID" min-width="50" header-align="center">
              <template slot-scope="types">
                <span>{{types.row.id}}</span>
              </template>
            </el-table-column>
            <el-table-column label="类型" min-width="120" header-align="center">
              <template slot-scope="types">
                <span>{{types.row.typeCode}}</span>
              </template>
            </el-table-column>
            <el-table-column label="类型名称" min-width="120" header-align="center">
              <template slot-scope="types">
                <span>{{types.row.typeName}}</span>
              </template>
            </el-table-column>
            <el-table-column label="操作"  min-width="180"
                      header-align="center">
                      <template  slot-scope="scope">
                        <el-button type="primary" size="small" icon="edit" style="width:65px"
                                   @click='handleUpdate(scope.row)'>更新</el-button>
                        <el-button type="success" size="small" icon="delete" style="width:65px"
                                   @click='cDeleteType(scope.row.id)'>删除</el-button>
                      </template>
            </el-table-column>
          </el-table>
        </el-col>
    </el-row>
    <el-dialog title="编辑类型" :visible.sync='editTypeVisible' size="tiny">
      <el-form :model="currentType" label-position="left" label-width="70px">
        <el-form-item label="原类型">
          <el-input v-model="currentType.typeCode" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="新类型">
          <el-input v-model="currentType.typeCodeNew"></el-input>
        </el-form-item>
        <el-form-item label="类型名称">
          <el-input v-model="currentType.typeName"></el-input>
        </el-form-item>
        <el-form-item label="是否父类">
          <el-switch
            @change="changeAction"
            v-model="currentType.rank"
            on-color="#13ce66"
            off-color="#ff4949"
            on-text="父类"
            off-text="子类"
            on-value="father"
            off-value="children">
        </el-switch>
        </el-form-item>
        <el-form-item label="类别树" v-show='tree'>
            <el-select v-model="currentType.type" placeholder="请选择父类" size="large" 
            filterable >
            <el-option
              v-for="item in currentType.types"
              :key="item.id"
              :label="item.typeName"
              :value="item.id"
              :disabled="item.disabled">
              <span style="float: left">{{item.typeName}}</span>
              <span style="float:right;color:#8492a6; font-size: 13px">{{item.typeCode}}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button   v-loading.fullscreen.lock="fullscreenLoading"type="primary" @click="cUpdateType">确认</el-button>
          <el-button @click='editTypeVisible=false'>关闭</el-button>
       </el-form-item>
      </el-form>
    </el-dialog>
    <el-dialog ref='type-detail' :title="textMap[dialogStatus]" :visible.sync='dialogTypeVisible' size="tiny">
      <el-form  :model="currentType" label-position="left" label-width="70px">
        <el-form-item label="类型">
          <el-input v-model="currentType.typeCode"></el-input>
        </el-form-item>
        <el-form-item label="类型名称">
          <el-input v-model="currentType.typeName"></el-input>
        </el-form-item>
        <el-form-item label="是否父类">
          <el-switch
            @change="changeAction"
            v-model="currentType.rank"
            on-color="#13ce66"
            off-color="#ff4949"
            on-text="父类"
            off-text="子类"
            on-value="father"
            off-value="children">
        </el-switch>
        </el-form-item>
        <el-form-item label="类别树" v-show='tree'>
            <el-select v-model="currentType.type" placeholder="请选择父类" size="large" 
            filterable >
            <el-option
              v-for="item in currentType.types"
              :key="item.id"
              :label="item.typeName"
              :value="item.id"
              :disabled="item.disabled">
              <span style="float: left">{{item.typeName}}</span>
              <span style="float:right;color:#8492a6; font-size: 13px">{{item.typeCode}}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button v-if="dialogStatus=='add'" type="primary" @click="cAddType">确认</el-button>
          <el-button v-else type="primary" @click="cUpdateType">确认</el-button>
          <el-button @click='dialogTypeVisible=false'>关闭</el-button>
      </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
    import {getTypes,updateBatchType,updateType,addType,deleteType} from '@/api/type'
    export default{
      data(){
        return {
            fullscreenLoading:false,
            typeControl:false,
            tree:false,
            dialogStatus:'',
            updateTypeId:null,
            dialogTypeVisible:false,
            editTypeVisible:false,
            action:'father',
            TypeDatas:null,
            currentType:{
              id:null,
              typeId:null,
              typeCode:null,
              typeCodeNew:null,
              typeName:null,
              rank:'father',
              type:0,
              types:[]
            },
            textMap: {
              update: '编辑类型',
              add: '新增类型'
            },
          }
      },
      created(){
        var self=this
        self.queryType()
      },
      methods:{
        initSelect(){
          var self=this;
          self.currentType.types=[]
          var j=0,len=self.TypeDatas.length
          for(;j<len;j++){
            if(self.TypeDatas[j].typeCode==self.currentType.typeCode)
            {
              self.currentType.types.push({
              id:self.TypeDatas[j].id,
              typeCode:self.TypeDatas[j].typeCode,
              typeName:self.TypeDatas[j].typeName,
              disabled:true
              })
            }else{
              self.currentType.types.push({
                id:self.TypeDatas[j].id,
                typeCode:self.TypeDatas[j].typeCode,
                typeName:self.TypeDatas[j].typeName
              })
            }
          }
        },
        resetState(){
          var self=this;
          self.currentType.typeName=''
          self.currentType.typeCode=''
        },
        handleAdd(){
          var self=this;
          self.dialogStatus='add'
          self.dialogTypeVisible=true
          self.typeControl=false
          self.resetState()
        },
        handleUpdate(currentRow){
          var self=this;
          // self.dialogStatus='update'
          // self.dialogTypeVisible=true;
          self.editTypeVisible=true;
          self.typeControl=true;
          self.currentType.typeCode=currentRow.typeCode;
          self.currentType.typeName=currentRow.typeName;
          self.currentType.id=currentRow.id;
        },
        changeAction(){
          var self=this;
          if(self.currentType.rank=='father'){
              self.tree=false;
              self.currentType.type=0;
          }
          if(self.currentType.rank=='children'){
              self.tree=true;
              self.initSelect();
          }
        },
        queryType(){
          var self=this;
          getTypes()
          .then((response)=>{
            self.TypeDatas=response.data
          })
        },
        cAddType(){
            var self=this;
            addType(self.currentType.type,self.currentType.typeName,self.currentType.typeCode)
            .then((response)=>{
                if(response.succ==true){
                  self.$message({
                    type:'success',
                    message:'添加成功！'
                  });
                  self.dialogTypeVisible=false
                  self.queryType()
                }else{
                  self.$message.error(response.msg)
                }
            })
            
        },
        cUpdateType(){
        var self=this;
        if(self.currentType.typeCodeNew!=null){
          self.fullscreenLoading=true
          updateBatchType(self.currentType.id,self.currentType.typeCode,self.currentType.typeCodeNew)
          .then((response)=>{
              if(response.succ==true){
                self.$message({
                  type:'success',
                  message:'更新成功！'
                });
                self.fullscreenLoading=false
                self.editTypeVisible=false
                self.queryType()
              }else{
                self.fullscreenLoading=false
                self.$message.error(response.msg)
              }
            })
          }
          updateType(self.currentType.type,self.currentType.id,self.currentType.typeName)
          .then((response)=>{
            if(response.succ==true){
              if(self.currentType.typeCodeNew==null){
                  self.$message({
                  type:'success',
                  message:'更新成功！'
                 });
                self.editTypeVisible=false
                self.queryType()
              }  
            }else{
              self.$message.error(response.msg)
            }
          })
        },
        cDeleteType(pTypeId){
            var self=this;
            self.$confirm('确认删除该类型？','提示',{
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(()=>{
             deleteType(pTypeId)
            .then((response)=>{
              if(response.succ==true){
                self.$message({
                  type:'success',
                  message:'删除成功！'
                });
                self.queryType()
              }else{
                self.$message.error(response.msg)
              }
            })
          })
        }
      }
    }
</script>


