import Router from 'vue-router'
import Login from '@/components/Login'
import TabPage from '@/components/TabPage'
import Container from '@/components/Container'
import AnnoPage from '@/components/AnnoPage'
import BrowseData from '@/components/BrowseData'
import QueryType from '@/components/QueryType'
import UnRecognizePage from '@/components/UnRecognizePage'
import AnnoQueryPage from '@/components/AnnoQueryPage'
import TypeManagePage from '@/components/TypeManagePage'
import KnowledgePage from '@/components/KnowledgePage'
import ModalTerm from '@/components/ModalTerm'
import TermPage from '@/components/TermPage'
import AtomicTermPage from '@/components/AtomicTermPage'
import ConceptPage from '@/components/ConceptPage'
export default new Router({
  mode:'history',
  routes: [
    {
      path:'/',
      name:'知识模块',
      component:Container,
      leaf:true,
      children:[
        {path:'/knowledgePage',component:KnowledgePage,name:'知识库'},
      ]
    },
    {
      path:'/',
      name:'标注模块',
      component:Container,
      children:[
        {path:'/annoPage',component:AnnoPage,name:'标注'},
        {path:'/unRecognizePage',component:UnRecognizePage,name:'待定'},
        {path:'/annoQueryPage',component:AnnoQueryPage,name:'标注查询'}
      ]
    },
    {
      path:'/',
      name:'术语模块',
      component:Container,
      children:[
        {path:'/typeManagePage',component:TypeManagePage,name:'元数据'},
        // {path:'/browseData',component:BrowseData,name:'浏览术语'},
        // {path:'/queryType',component:QueryType,name:'查询术语'},
        {path:'/termPage',component:TermPage,name:'标准术语'},
        {path:'/atomicTermPage',component:AtomicTermPage,name:'原子术语'},
        {path:'/conceptPage',component:ConceptPage,name:'概念'}
      ]
    },
    {
      path:'/login',
      name:'Login',
      component:Login,
      hidden:true
    },
    {
      path:'/',
      name:'excel操作',
      component:Container,
      leaf:true,
      children:[
        {path:'/excel',component:ModalTerm,name:'excel导出'},
      ]
    },
    {
      path:'*',
      hidden:true,
      redirect:{path:'/login'}
    }
  ]
})



