<template>
  <div>
    <el-row  :gutter="10" style="background-color:#F2F2F2;margin-top:10px;margin-bottom:5px;">
        <el-col :span="2">
            <el-button type="danger" @click='handleAddTerm' icon="plus">新增</el-button> 
        </el-col>
        <el-col :span="2">
          <el-button type="primary" @click='initTable' icon="search">查询</el-button>
        </el-col>
        <el-col :span="4" >
          <el-input v-model="condition.label" placeholder="标签" style="width:100%"
          @keyup.enter.native="initTable"></el-input>
        </el-col>
        <el-col :span="4" >
          <el-input v-model="condition.termName" placeholder="术语名称" style="width:100%"
          @keyup.enter.native="initTable"></el-input>
        </el-col>
        <el-col :span="4" >
          <el-select v-model="condition.termType" placeholder="术语类型" style="width:100%"
            filterable @change="initTable" >
            <el-option
              v-for="item in condition.types"
              :key="item.typeName"
              :label="item.typeName"
              :value="item.typeName">
              <!-- <span style="float: left">{{item.typeCode}}</span>
              <span style="float:right;color:#8492a6; font-size: 13px">{{item.typeName}}</span> -->
            </el-option>
          </el-select>    
        </el-col>
        <el-col :span="4">
          <el-checkbox v-model="condition.checked" size="large" @change="checkedChange">{{checkedName}}</el-checkbox>
        </el-col>
    </el-row>
    <el-table
        v-loading="loading"
        element-loading-text="加载数据"
        :data="termDatas"
        style="width:100%"  
        empty-text="暂无数据"
        highlight-current-row
        >              
        <!-- <el-table-column label="ID" property="id"  min-width="50"  header-align="center">
        </el-table-column> -->
        <el-table-column label="术语ID" property="termId"  min-width="130"  header-align="center">
        </el-table-column>
        <el-table-column label="标签"   min-width="200"  header-align="center">
          <template slot-scope="scope">
            <el-tag :key="tag"
                    v-for="tag in scope.row.labelArr"
                    type="success">
                    {{tag}}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="术语类型" property="termType" min-width="200" header-align="center">
        </el-table-column>
        <el-table-column label="原始名称" property="originName" min-width="120" header-align="center" > 
        </el-table-column> 
        <el-table-column label="术语名称" property="termName" min-width="120" header-align="center" >   
        </el-table-column>
        <el-table-column
          label="状态"
          min-width="100">
          <template slot-scope="scope">
            <el-tag
              :type="(scope.row.conceptId === null ||scope.row.conceptId === '') ? 'primary' : 'success'"
              close-transition>
              <div v-if="scope.row.conceptId != null && scope.row.conceptId != ''">
                {{scope.row.standardName}}
              </div>
              <div v-else>
                无同义词
              </div>
            </el-tag>
          </template>
        </el-table-column>
        <!-- <el-table-column label="术语集合" property="termType" min-width="160" header-align="center" >
        </el-table-column> -->
        <el-table-column label="操作"  min-width="250"
          header-align="center">
          <template  slot-scope="scope">
            <el-button-group>
              <el-button type="primary" size="small" icon="circle-check"
                @click='handleUpdateTerm(scope.row)'>更新</el-button>

              <el-button type="danger" size="small" icon="time"
                @click='handleDeleteTerm(scope.row.id)'>弃用</el-button>

              <el-button type="success" size="small"
                @click='handleConcept(scope.row)'>同义词</el-button>

              <el-button type="primary" size="small" icon="share" 
                @click='handleCategory(scope.row)'>标签</el-button>
             </el-button-group>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page.sync="pageIndex" :page-sizes="[10,20,30,50]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalLimit">
      </el-pagination>
      <el-dialog title="编辑术语" :visible.sync='editTermVisible' size="tiny">
        <el-form :model="currentTerm" label-position="left" label-width="70px">
          <el-form-item label="术语ID">
            <el-input v-model="currentTerm.termId" :disabled="true"></el-input>
          </el-form-item> 
          <el-form-item label="术语名称">
            <el-input v-model="currentTerm.termName"></el-input>
          </el-form-item>     
          <el-form-item label="是否新增">
            <el-switch
              v-model="currentTerm.rank"
              on-color="#13ce66"
              off-color="#ff4949"
              on-text="新增"
              off-text="更新"
              on-value="add"
              off-value="update">
          </el-switch>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="cUpdateTerm">确认</el-button>
            <el-button @click='editTermVisible=false'>关闭</el-button>
         </el-form-item>
        </el-form>
      </el-dialog>
      <el-dialog title="新增术语" :visible.sync='addTermVisible' size="tiny">
        <el-form :model="currentTerm" label-position="left" label-width="70px">
          <el-form-item label="术语ID">
            <el-input v-model="currentTerm.termId"></el-input>
          </el-form-item>
          <el-form-item label="父术语ID">
            <el-input v-model="currentTerm.ptermId"></el-input>
          </el-form-item>
          <el-form-item label="术语名称">
            <el-input v-model="currentTerm.termName"></el-input>
          </el-form-item>     
          <el-form-item label="术语编码">
            <el-input v-model="currentTerm.termCode"></el-input>
          </el-form-item>    
          <el-form-item label="术语集合">
            <el-input v-model="currentTerm.termType"></el-input>
          </el-form-item>    
          <el-form-item>
            <el-button type="primary" @click="cAddTerm">确认</el-button>
            <el-button @click='addTermVisible=false'>关闭</el-button>
         </el-form-item>
        </el-form>
      </el-dialog>
      <el-dialog title="同义词操作" :visible.sync='synonymVisible' size="tiny">
        <el-form :model="currentConcept" label-position="left" label-width="110px">
          <el-form-item label="当前对应概念">
            <el-input v-model="currentConcept.standName" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="是否创建新概念">
            <el-switch
              @change="switchChange"
              v-model="currentConcept.action"
              on-color="#13ce66"
              off-color="#ff4949"
              on-text="创建"
              off-text="归组"
              on-value="add"
              off-value="update">
            </el-switch>
          </el-form-item>
          <el-form-item label="标准概念集" v-show="conceptSelect">
            <el-select v-model="currentConcept.conceptValue" 
              filterable allow-create
              placeholder="请选择" style=" width:100%">
              <el-option
                v-for="item in currentConcept.concepts"
                :key="item.conceptId"
                :label="item.standardName"
                :value="item.conceptId">
              </el-option>
            </el-select>
          </el-form-item>       
          <el-form-item>
            <el-button type="primary" @click="cConceptConfirm">确认</el-button>
            <el-button @click='synonymVisible=false'>关闭</el-button>
         </el-form-item>
        </el-form>
      </el-dialog>
      <el-dialog title="当前术语标签" :visible.sync='categoryVisible' size="tiny" @close="categoryClose">
        <el-tag
          :key="tag"
          v-for="tag in dynamicTags"
          :closable="true"
          :close-transition="true"
          type="success"
          @close="handleClose(tag)"
        >
        {{tag}}
        </el-tag>
        <div v-if="selectVisible">
          <el-select v-model="category.tagArr" 
                filterable 
                allow-create
                multiple
                placeholder="请选择" 
                style=" width:80%"
                size="mini"
                @keyup.enter.native="handleSelectConfirm"
               >
                <el-option
                  v-for="item in category.tags"
                  :key="item.id"
                  :label="item.tagName"
                  :value="item.tagName">
                </el-option>
          </el-select>
          <el-button type="primary" @click="addTag" size="small">添加</el-button>
        </div>
        <el-button v-else class="button-new-tag" size="small" @click="showSelect">+ New Tag</el-button>
      </el-dialog>
  </div>
</template>
<script>
    import {getTerms,updateTerm,addTerm,deleteTerm,updateLabelofTerm,getTypesOfTerm} from '@/api/term'
    import {getAllConcepts,addConcept,updateConcept,getStandName,updateStandName} from '@/api/concept'
    import {getAllTags,addTag,addBatchTag} from '@/api/tag'
    import {getTypes} from '@/api/type'
    export default{
      data () {
        return {
          checkedName:'仅显示有同义词的数据',
          termDatas:null,
          loading:false,
          pageIndex:0,
          totalPages:0,
          totalLimit:0,
          pageSize:10,
          editTermVisible:false,
          addTermVisible:false,
          synonymVisible:false,
          currentTerm:{
            id:null,
            termId:null,
            ptermId:null,
            termCode:null,
            termType:null,
            termName:null,
            originName:null,
            rank:'update'
          },
          currentConcept:{
            concepts:[],
            conceptValue:'',
            conceptName:null,
            action:'add',
            termId:null,
            ptermId:null,
            termCode:null,
            termType:null,
            termName:null,
            category:null,
            originName:null,
            standName:null,
            conceptId:null,
            id:null
          },
          conceptSelect:false,
          categoryVisible:false,
          dynamicTags:[],
          selectVisible: false,
          category:{
            tagArr:[],
            tags:null,
            id:null,
            serverLabel:''
          },
          condition:{
            label:null,
            termName:null,
            termType:null,
            types:[],
            checked:true
          }
        }
      },
      created(){
        var self=this
        self.initTable()
        self.queryType()
      },
      methods: {
        checkedChange(){
          var self=this
          if(self.condition.checked==false){
            self.checkedName='仅显示无同义词的数据'
          }else{
            self.checkedName='仅显示有同义词的数据'
          }
          self.initTable()
        },
        queryType(){
          var self=this;
          getTypesOfTerm()
          .then((response)=>{
            console.log(response)
            Object.keys(response.data).forEach((key)=>{
              self.condition.types.push({typeName:response.data[key]})
            })
          })
          // getTypes()
          // .then((response)=>{
          //   console.log(response)
          //   self.condition.types=response.data
          // })
        },
        categoryClose(){
          var self=this
          //todo 调用后台的接口，更新术语表中的category字段
          if(self.dynamicTags.length>0){
            var tempStr='['
            Object.keys(self.dynamicTags).forEach((key)=>{
              tempStr+='"'+self.dynamicTags[key]+'",'
            })
            self.category.serverLabel=tempStr.substring(0,tempStr.length-1)+']'
            console.log(self.category.serverLabel)
            updateLabelofTerm(self.category.id,self.category.serverLabel)
            .then((response)=>{
                self.initTable()
            })
            self.dynamicTags=[]  
          }    
        },
        addTag(){
          var self=this
          console.log(self.category.tagArr)
          if(self.category.tagArr!=null&&self.category.tagArr.length>0)
          {
            self.dynamicTags.push(...self.category.tagArr)
          }
          //todo 调用后台的接口，将新增的标签插入到标签表中，前台做筛选，把新增的tag传入到后台
          var serverArr=[]
          Object.keys(self.category.tagArr).forEach((key)=>{
            for(var k=0;k<self.category.tags.length;k++){
              if(self.category.tagArr[key]==self.category.tags[k]['tagName']){
                  break
                }else{
                if((k+1)==self.category.tags.length){
                  serverArr.push(self.category.tagArr[key])
                }
              }
            }
          })       
          addBatchTag(serverArr)
          .then((response)=>{
            if(response.succ==true)
            {
              getAllTags()
              .then((response)=>{
                if(response.succ==true){
                  self.category.tags=response.data
                }
              });
            }
          })
          self.selectVisible = false;
          self.category.tagArr =[];
        },
        handleClose(tag){
          var self=this
          self.dynamicTags.splice(self.dynamicTags.indexOf(tag), 1);
        },
        showSelect(){
          var self=this
          self.selectVisible = true;
        },
        handleSelectConfirm(){
          
        },
        handleCategory(currentRow){
          var self=this
          console.log(currentRow)
          self.categoryVisible=true
          self.selectVisible=false
          self.category.id=currentRow.id
          //todo 做两个事情，1.取出当前行的category字段按一定的规则拆分出来，push到dynamicTags;2.初始化下拉框
          getAllTags()
          .then((response)=>{
            if(response.succ==true){
              self.category.tags=response.data
            }
          });         
          let labelStr=currentRow.label
          if(labelStr){
            var tempStr=labelStr.substring(labelStr.indexOf('[')+1,labelStr.indexOf(']'))
            var tagStrArr=tempStr.split(',')
            console.log(tagStrArr)
            Object.keys(tagStrArr).forEach((key)=>{
              self.dynamicTags.push(tagStrArr[key].substring(1,tagStrArr[key].length-1))
            })
          }
        },
        cConceptConfirm(){
          var self=this
          if(self.currentConcept.action=='add'){
            if(self.currentConcept.conceptId==null||self.currentConcept.conceptId==''){
              addConcept(self.currentConcept.id,self.currentConcept.originName)
              .then((response)=>{
                if(response.succ==true){
                  self.$message({
                      type:'success',
                      message:'新增成功！'
                  });
                  self.initTable()
                }else{
                  self.$message.error(response.msg)
                }
                self.synonymVisible=false
              })
            }else{
              self.$message({
                type:'warning',
                message:'已有对应概念，无法再次新增！'
              });
            }
          }
          if(self.currentConcept.action=='update'){
            if(self.currentConcept.conceptValue==null||self.currentConcept.conceptValue==''){
              self.$message({
                type:'warning',
                message:'请先选择下拉框的选项或者输入概念！'
              });
              return
            }
            var conceptArr=self.currentConcept.concepts;
            for(var k=0;k<conceptArr.length;k++){
              Object.keys(conceptArr[k]).forEach((key)=>{
                if(conceptArr[k][key]===self.currentConcept.conceptValue){
                  self.currentConcept.conceptName=conceptArr[k]['standardName']
                }else{
                  if(k===(conceptArr.length-1)){
                    var cup=self.currentConcept.conceptValue;
                    self.currentConcept.conceptValue=self.currentConcept.conceptName
                    self.currentConcept.conceptName=cup
                  }
                }
              })
            }
            updateConcept(self.currentConcept.id,self.currentConcept.conceptValue,self.currentConcept.conceptName)
            .then((response)=>{
              if(response.succ==true){
                self.$message({
                    type:'success',
                    message:'更新成功！'
                });
                self.initTable()
              }else{
                self.$message.error(response.msg)
              }
              self.synonymVisible=false
            })
          }
        },
        switchChange(){
          var self=this
          if(self.currentConcept.action=='add')
          {
            self.conceptSelect=false
          }
          if(self.currentConcept.action=='update')
          {
            self.conceptSelect=true
            getAllConcepts()
            .then((response)=>{
              if(response.succ==true){
                self.currentConcept.concepts=response.data
              }else{
                self.$message.error(response.msg)
              }
            })
          }
        },
        handleConcept(currentRow){
          //同义词操作
          var self=this
          self.currentConcept.standName=null
          self.synonymVisible=true
          self.currentConcept.originName=currentRow.originName;
          self.currentConcept.conceptId=currentRow.conceptId;
          self.currentConcept.id=currentRow.id;
          if(currentRow.conceptId!=null&&currentRow.conceptId!=''){
            getStandName(currentRow.conceptId)
            .then((response)=>{
              if(response.succ==true){
                self.currentConcept.standName=response.data.standardName
              }
            })
          }
        },
        handleSizeChange(val){
          var self=this;
          self.pageSize=val;
          self.initTable();
        },
        handleCurrentChange(val){
          var self=this;
          self.pageIndex=val;
          self.initTable();
        },
        initTable(){
          var self=this
          self.loading=true
          getTerms(self.pageIndex,self.pageSize,self.condition.termName,self.condition.termType,
            self.condition.label,self.condition.checked)
          .then((response)=>{
            if(response.succ==true){
              self.totalLimit=response.data.total;
              var termArr=response.data.dataList
              Object.keys(termArr).forEach((key)=>{
                console.log(termArr[key])
                if(termArr[key].label!=null&&termArr[key].label!=''){
                  termArr[key].labelArr=[]
                  var tempStr=termArr[key].label.substring(termArr[key].label.indexOf('[')+1,termArr[key].label.indexOf(']'))
                  termArr[key].labelArr=tempStr.split(',') 
                }             
              })
              self.termDatas=termArr
            }
            self.loading=false
          })
        },
        cUpdateTerm(){
          var self=this;
          if(self.currentTerm.rank=='add'){
            addTerm(self.currentTerm.termId,self.currentTerm.ptermId,self.currentTerm.termName,
              self.currentTerm.termCode,self.currentTerm.termType,self.currentTerm.originName)
                .then((response)=>{
                  if(response.succ==true){
                  self.$message({
                    type:'success',
                    message:'添加成功！'
                  });
                  self.editTermVisible=false
                  self.initTable()
              }else{
                self.$message.error(response.msg)
              }
            })
          }
          if(self.currentTerm.rank=='update'){
              updateTerm(self.currentTerm.id,self.currentTerm.termId,self.currentTerm.termName)
              .then((response)=>{
              if(response.succ==true){
                  self.$message({
                  type:'success',
                  message:'更新成功！'
                  });
                  self.editTermVisible=false
                  self.initTable()
              }else{
                self.$message.error(response.msg)
              }
            })
          }
        },
        cAddTerm(){
          var self=this;
          addTerm(self.currentTerm.termId,self.currentTerm.ptermId,self.currentTerm.termName,
                self.currentTerm.termCode,self.currentTerm.termType,self.currentTerm.termName)
              .then((response)=>{
                  if(response.succ==true){
                  self.$message({
                    type:'success',
                    message:'添加成功！'
                  });
                  self.addTermVisible=false
                  self.initTable()
                }else{
                  self.$message.error(response.msg)
                }
              })
        },
        handleAddTerm(){
          var self=this
          self.addTermVisible=true
          self.currentTerm.id=null
          self.currentTerm.termId=null
          self.currentTerm.ptermId=null
          self.currentTerm.termName=null
          self.currentTerm.termCode=null
          self.currentTerm.termType=null
          self.currentTerm.originName=null
        },
        handleUpdateTerm(currentRow){
          var self=this;
          console.log(currentRow)
          self.currentTerm.id=currentRow.id;
          self.currentTerm.termId=currentRow.termId;
          self.currentTerm.ptermId=currentRow.ptermId;
          self.currentTerm.termName=currentRow.termName;
          self.currentTerm.termCode=currentRow.termCode;
          self.currentTerm.termType=currentRow.termType;
          self.currentTerm.originName=currentRow.originName;
          self.editTermVisible=true;
        },
        handleDeleteTerm(pId){
            var self=this;
            self.$confirm('确认遗弃该类型？','提示',{
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(()=>{
             deleteTerm(pId)
            .then((response)=>{
              if(response.succ==true){
                self.$message({
                  type:'success',
                  message:'删除成功！'
                });
                self.initTable()
              }else{
                self.$message.error(response.msg)
              }
            })
          })
        }
      }
    }
</script>



