<template>
  <div>
    <el-row  :gutter="10" style="background-color:#F2F2F2;margin-top:10px;margin-bottom:5px;">
        <el-col :span="2">
          <el-button type="primary" @click='initTable' icon="search">查询</el-button>
        </el-col>
        <el-col :span="4" :offset="1">
          <el-input v-model="condition.term" placeholder="术语名称" style="width:100%"
          @keyup.enter.native="termEnter"></el-input>
        </el-col>
        <el-col :span="4" >
          <el-select v-model="condition.type" placeholder="术语类型" style="width:100%"
            filterable @change="typeChange" >
            <el-option
              v-for="item in condition.types"
              :key="item.typeCode"
              :label="item.typeCode"
              :value="item.typeCode">
              <span style="float: left">{{item.typeCode}}</span>
              <span style="float:right;color:#8492a6; font-size: 13px">{{item.typeName}}</span>
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="4">
          <el-checkbox v-model="condition.checked" size="large" @change="checkedChange">{{checkedName}}</el-checkbox>
        </el-col>
    </el-row>
    <el-table
        v-loading="loading"
        element-loading-text="加载数据"
        :data="termDatas"
        style="width:100%"  
        empty-text="暂无数据"
        highlight-current-row
        >              
        <el-table-column label="ID" property="id"  min-width="100"  header-align="center">
        </el-table-column>
        <el-table-column label="术语" property="term" min-width="160" header-align="center" >   
        </el-table-column>
        <el-table-column label="类型" property="type" min-width="160" header-align="center" >
        </el-table-column>
        <el-table-column
          prop="conceptId"
          label="同义词"
          min-width="80"
          filter-placement="bottom-end">
          <template slot-scope="scope">
            <el-tag
              :type="(scope.row.conceptId === null ||scope.row.conceptId === '') ? 'primary' : 'success'"
              close-transition>
              <div v-if="scope.row.conceptId != null && scope.row.conceptId != ''">
                {{scope.row.standardName}}
              </div>
              <div v-else>
                无同义词
              </div>
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作"  min-width="250"
          header-align="center">
          <template  slot-scope="scope">
            <el-button-group>
            <el-button type="danger" size="small" icon="time"
                       @click='handleDeleteTerm(scope.row.id)'>弃用</el-button>
            <el-button type="success" size="small"
                       @click='handleConcept(scope.row)'>同义词</el-button>
            <el-button type="primary" size="small"
                       @click='handleAnnotation(scope.row)'>上下文</el-button>
           <el-button type="primary" size="small" icon="edit"
                       @click='handleUpdate(scope.row)'>更新</el-button>
            </el-button-group>
          </template>
        </el-table-column>
        <el-table-column label="更新时间" property="gmtModified" min-width="160" header-align="center" >
        </el-table-column>
      </el-table>
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page.sync="pageIndex" :page-sizes="[10,20,30,50]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalLimit">
      </el-pagination>
      <el-dialog title="同义词操作" :visible.sync='synonymVisible' size="tiny">
        <el-form :model="currentConcept" label-position="left" label-width="110px">
          <el-form-item label="当前对应概念">
            <el-input v-model="currentConcept.standName" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="是否创建新概念">
            <el-switch
              @change="switchChange"
              v-model="currentConcept.action"
              on-color="#13ce66"
              off-color="#ff4949"
              on-text="创建"
              off-text="归组"
              on-value="add"
              off-value="update">
            </el-switch>
          </el-form-item>
          <el-form-item label="标准概念集" v-show="conceptSelect">
            <el-select v-model="currentConcept.conceptValue" 
              filterable allow-create
              placeholder="请选择" style=" width:100%">
              <el-option
                v-for="item in currentConcept.concepts"
                :key="item.conceptId"
                :label="item.standardName"
                :value="item.conceptId">
              </el-option>
            </el-select>
          </el-form-item>       
          <el-form-item>
            <el-button type="primary" @click="cConceptConfirm">确认</el-button>
            <el-button @click='synonymVisible=false'>关闭</el-button>
         </el-form-item>
        </el-form>
      </el-dialog>
      <el-dialog title="修改类型" :visible.sync='updateAtomicTermVisible' size="tiny">
        <el-form :model="currentAtomicTerm" label-position="left" label-width="110px">
          <el-form-item label="原子术语">
            <el-input v-model="currentAtomicTerm.term"></el-input>
          </el-form-item>
          <el-form-item label="当前类型">
            <el-input v-model="currentAtomicTerm.type"></el-input>
          </el-form-item>
          <el-form-item label="类型列表">
            <el-select v-model="currentAtomicTerm.value" 
              filterable 
              allow-create
              placeholder="请选择" style=" width:100%">
              <el-option
                v-for="item in currentAtomicTerm.types"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                <span style="float: left">{{item.label}}</span>
                <span style="float:right;color:#8492a6; font-size: 13px">{{item.value}}</span>
              </el-option>
            </el-select>
          </el-form-item>       
          <el-form-item>
            <el-button type="primary" @click="cAtomicTermConfirm">确认</el-button>
            <el-button @click='updateAtomicTermVisible=false'>关闭</el-button>
         </el-form-item>
        </el-form>
      </el-dialog>
      <modal-annotation ref='annotationDialog' ></modal-annotation>
  </div>
</template>
<script>
    import ModalAnnotation from '@/components/ModalAnnotation.vue'
    import {fuzzyAtomicTerms,getAtomicTerms,getAllConcepts,addConcept,updateConcept,deleteAtomicTerm,updateAtomicTerm} from '@/api/atomic'
    import {getStandName} from '@/api/concept'
    import {getTypes,getTermAnnotationsByType} from '@/api/type'
    import {config,configFromDb} from '@/api/annotation'
    export default{
      data () {
        return {
          updateAtomicTermVisible:false,
          checkedName:'仅显示有同义词的数据',
          newTermDatas:null,
          termDatas:null,
          loading:false,
          pageIndex:0,
          totalPages:0,
          totalLimit:0,
          pageSize:10,        
          currentConcept:{
            concepts:[],
            conceptValue:'',
            conceptName:null,
            action:'add',
            termId:null,
            ptermId:null,
            termCode:null,
            termType:null,
            termName:null,
            category:null,
            originName:null,
            standName:null,
            conceptId:null,
            id:null
          },
          conceptSelect:false,
          condition:{
            term:null,
            type:null,
            checked:true,
            types:[]
          },
          synonymVisible:false,
          bratConfig:null,
          param:{
            config:null,
            term:null,
            type:null
          },
          currentAtomicTerm:{
            type:null,
            types:[],
            term:null,
            value:null
          }
        }
      },
      created(){
        var self=this
        self.initTable()
        self.queryType()
      },
      methods: {
        typeChange(){
          var self=this
          console.log('type')
          if(self.condition.type!=null){
            self.condition.term=null
          }
          self.initTable()
        },
        termEnter(){
          var self=this
          console.log('term')
          self.condition.type=null
          self.initTable()
        },
        cAtomicTermConfirm(){
          var self=this
          updateAtomicTerm(self.currentAtomicTerm.atomicTermId,self.currentAtomicTerm.value)
          .then((response)=>{
            if(response.succ==true){
              self.updateAtomicTermVisible=false
              self.$message({
                      type:'success',
                      message:'更新成功！'
              });
              self.initTable()
            }
          })
        },
        getTypesFromConfig(){
          var self=this
          if(self.currentAtomicTerm.types==null||self.currentAtomicTerm.types.length==0){
            configFromDb()
            .then((response)=>{
                var serverTypes=response.data
                var i=0,leni=serverTypes.length
                for(;i<leni;i++){
                  self.currentAtomicTerm.types.push({
                    label: serverTypes[i].typeName,
                    value: serverTypes[i].typeCode
                  })
                }
            })
          }
        },
        handleUpdate(currentRow){
          var self=this
          self.getTypesFromConfig()
          self.currentAtomicTerm.atomicTermId=currentRow.id
          self.currentAtomicTerm.term=currentRow.term
          self.currentAtomicTerm.type=currentRow.type
          // self.currentAtomicTerm.value=currentRow.type
          self.updateAtomicTermVisible=true
        },
        checkedChange(){
          var self=this
          if(self.condition.checked==false){
            self.checkedName='仅显示无同义词的数据'
          }else{
            self.checkedName='仅显示有同义词的数据'
          }
          self.initTable()
        },
        getBratConfig(){
          if (self.bratConfig==null){
            return config();
          }else{
            return () => {
              return self.bratConfig;
            }
          }
        },
        handleAnnotation(currentRow){
          var self = this;
          self.param.type=currentRow.type
          self.param.term=currentRow.term
          self.$refs.annotationDialog.open(self.param,self.getBratConfig
            // , () => {
            // return getTermAnnotationsByType(currentRow.type,currentRow.term)
          // }
          );
        },
        queryType(){
          var self=this;
          getTypes()
          .then((response)=>{
            self.condition.types=response.data
          })
        },
        cConceptConfirm(){
          var self=this
          if(self.currentConcept.action=='add'){
            if(self.currentConcept.conceptId==null||self.currentConcept.conceptId==''){
              addConcept(self.currentConcept.id,self.currentConcept.originName)
              .then((response)=>{
                if(response.succ==true){
                  self.$message({
                      type:'success',
                      message:'新增成功！'
                  });
                  self.initTable()
                }else{
                  self.$message.error(response.msg)
                }
                self.synonymVisible=false
              })
            }else{
              self.$message({
                  type:'warning',
                  message:'已有对应概念，无法再次新增！'
              });
            }
          }
          if(self.currentConcept.action=='update'){
            if(self.currentConcept.conceptValue==null||self.currentConcept.conceptValue==''){
              self.$message({
                  type:'warning',
                  message:'请先选择下拉框的选项或者输入概念！'
              });
              return
            }
            var conceptArr=self.currentConcept.concepts;
            for(var k=0;k<conceptArr.length;k++){
              Object.keys(conceptArr[k]).forEach((key)=>{
                  if(conceptArr[k][key]===self.currentConcept.conceptValue){
                    self.currentConcept.conceptName=conceptArr[k]['standardName']
                  }else{
                    if(k===(conceptArr.length-1)){
                      var cup=self.currentConcept.conceptValue;
                      self.currentConcept.conceptValue=self.currentConcept.conceptName
                      self.currentConcept.conceptName=cup
                    }
                  }
              })
            }
            updateConcept(self.currentConcept.id,self.currentConcept.conceptValue,self.currentConcept.conceptName)
            .then((response)=>{
              if(response.succ==true){
                self.$message({
                    type:'success',
                    message:'更新成功！'
                });
                self.initTable()
              }else{
                self.$message.error(response.msg)
              }
              self.synonymVisible=false
            })
          }
        },
        switchChange(){
          var self=this
          if(self.currentConcept.action=='add')
          {
            self.conceptSelect=false
          }
          if(self.currentConcept.action=='update')
          {
            self.conceptSelect=true
            getAllConcepts()
            .then((response)=>{
              if(response.succ==true){
                self.currentConcept.concepts=response.data
              }else{
                self.$message.error(response.msg)
              }
            })
          }
        },
        handleConcept(currentRow){
          //同义词操作
          var self=this
          self.currentConcept.standName=null
          self.synonymVisible=true
          self.currentConcept.originName=currentRow.term;
          self.currentConcept.conceptId=currentRow.conceptId;
          self.currentConcept.id=currentRow.id;
          if(currentRow.conceptId!=null&&currentRow.conceptId!=''){
            getStandName(currentRow.conceptId)
            .then((response)=>{
              if(response.succ==true){
                self.currentConcept.standName=response.data.standardName
              }
            })
          }
        },
        handleSizeChange(val){
          var self=this;
          self.pageSize=val;
          self.initTable();
        },
        handleCurrentChange(val){
          var self=this;
          self.pageIndex=val;
          self.initTable();
        },
        initTable(){
          var self=this
          self.loading=true
          if(self.condition.term!=null&&self.condition.type==null)
          {
            fuzzyAtomicTerms(self.pageIndex,self.pageSize,self.condition.term,self.condition.type,self.condition.checked)
            .then((response)=>{
              self.totalLimit=response.data.total;
              self.termDatas=response.data.dataList
              self.loading=false
            })
          }else{
            getAtomicTerms(self.pageIndex,self.pageSize,self.condition.term,self.condition.type,self.condition.checked)
            .then((response)=>{
              self.totalPages=response.data.pages;
              self.totalLimit=response.data.total;
              self.termDatas=response.data.dataList
              self.loading=false
            })
          }
        },
        handleDeleteTerm(pId){
            var self=this;
            self.$confirm('确认遗弃该类型？','提示',{
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(()=>{
             deleteAtomicTerm(pId)
            .then((response)=>{
              if(response.succ==true){
                self.$message({
                  type:'success',
                  message:'删除成功！'
                });
                self.initTable()
              }else{
                self.$message.error(response.msg)
              }
            })
          })
        }
      }
      ,
      components:{
        'modal-annotation':ModalAnnotation
      }
    }
</script>



