import myFetch from '@/utils/myFetch'
import Qs from 'qs' 
export function getTypesOfTerm(){
	var param=Qs.stringify({
	});
	return  myFetch({
		method:'post',
		url:'/term/getTypesOfTerm.do',
		data:param
	});
}
export function getTerms(pPageIndex,pPageSize,pTermName,pTermType,pLabel,pChecked){
	var param=Qs.stringify({
		'pageNum':pPageIndex,
		'pageSize':pPageSize,
		'termName':pTermName,
		'termType':pTermType,
		'label':pLabel,
		'checked':pChecked	
	});
	return  myFetch({
		method:'post',
		url:'/term/queryByCondition.do',
		data:param
	});
}
export function updateLabelofTerm(pId,pLabel){
	var param=Qs.stringify({
		 id:pId,
		'label':pLabel
	});
	return  myFetch({
		method:'post',
		url:'/term/updateLabelOfTerm.do',
		data:param
	});
}
export function updateTerm(pId,pConcepId,pConceptName){
	var param=Qs.stringify({
		 id:pId,
		'conceptId':pConcepId,
		'conceptName':pConceptName
	});
	return  myFetch({
		method:'post',
		url:'/term/updateTerm.do',
		data:param
	});
}
export function addTerm(pConcepId,pPConcepId,pConceptName,pConceptCode,pConceptType,pOriginName){
	var param=Qs.stringify({
		'conceptId':pConcepId,
		'pconceptId':pPConcepId,
		'conceptName':pConceptName,
		'conceptCode':pConceptCode,
		'conceptType':pConceptType,
		'originName':pOriginName
	});
	return  myFetch({
		method:'post',
		url:'/term/addTerm.do',
		data:param
	});
}
export function deleteTerm(pId){
	var param=Qs.stringify({
		'id':pId
	});
	return  myFetch({
		method:'post',
		url:'/term/deleteTerm.do',
		data:param
	});
}


